export default function Page({ params }: { params: Promise<{ item: string }> }) {
  const { item } = use(params)
  const router = useRouter()

  return (
  <>
    <div className="text-end">
    <Button variant="destructive" onClick={() => router.back()}>
      Quay lại
    </Button>
    </div>

    <Card>
      <CardHeader>
        <CardTitle>Thư viện của tôi</CardTitle>
        <div className="flex items-center justify-between mt-2">
          <div className="flex items-center space-x-2">
            <Input placeholder="Tìm kiếm..." className="w-[200px]" />
            <Button variant="outline" size="icon">
              <FilterIcon className="h-4 w-4" />
            </Button>
          </div>
        </div>
      </CardHeader>

      <CardContent>
        {/* Nội dung thư viện ở đây */}
      </CardContent>
      <CardFooter>
        <Pagination>
      <PaginationContent>
        <PaginationItem>
          <PaginationPrevious href="#" />
        </PaginationItem>
        <PaginationItem>
          <PaginationLink href="#">1</PaginationLink>
        </PaginationItem>
        <PaginationItem>
          <PaginationLink href="#" isActive>
            2
          </PaginationLink>
        </PaginationItem>
        <PaginationItem>
          <PaginationLink href="#">3</PaginationLink>
        </PaginationItem>
        <PaginationItem>
          <PaginationEllipsis />
        </PaginationItem>
        <PaginationItem>
          <PaginationNext href="#" />
        </PaginationItem>
      </PaginationContent>
    </Pagination>
      </CardFooter>

</Card>
  </>
)

  
}



